import React from "react";
import { Card, CardBody, CardHeader, List } from "reactstrap";
import styled from "styled-components";
import { ItemRow } from "../../utils/item-row";

const PageWrapper = styled.div`
  padding: 0 2em;

  .title {
    font-size: 1.2rem;
    font-weight: 500;
  }

  .content {
    width: 100%;
    padding: 1em 1.5em;

    .card-box {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-wrap: wrap;
      gap: 20px;

      & > div {
        flex: 1;
        min-width: 250px;
        max-width: 100%;
      }
    }
  }
`;

export const ResultComparisonComponent = ({ setPageState }) => {
  return (
    <PageWrapper>
      <h2 className="title">Comparison Results</h2>

      <div className="content">
        <div className="card-box">
          <Card>
            <CardHeader className="bold large">File 1.csv</CardHeader>

            <CardBody>
              <List>
                <ItemRow title="Total Records" content={450} />
                <ItemRow title="Matching Record" content={442} />
                <ItemRow title="Unmatched records" content={8} />
              </List>
            </CardBody>
          </Card>
          <Card>
            <CardHeader className="bold large">File 2.csv</CardHeader>

            <CardBody>
              <List>
                <ItemRow title="Total Records" content={452} />
                <ItemRow title="Matching Record" content={442} />
                <ItemRow title="Unmatched records" content={10} />
              </List>
            </CardBody>
          </Card>
        </div>

        <div className="centered mt-4">
          <button
            type="button"
            onClick={() =>
              setPageState((prev) => ({ ...prev, hasMatchedResult: true }))
            }
            className="btn btn-primary"
          >
            Unmatched Report
          </button>
        </div>
      </div>
    </PageWrapper>
  );
};
