import React from "react";
import styled from "styled-components";

const UploadWrapper = styled.div`
  padding: 0 2em;

  & > * + * {
    margin-top: 2em;
  }

  .title {
    font-size: 1.2rem;
    font-weight: 500;
  }

  .content {
    display: flex;
    align-items: flex-end;
    justify-content: flex-start;
    gap: 20px;

    .upload-box {
      width: 100%;
      flex: 1;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
      gap: 15px;

      label {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        gap: 10px;

        span {
          display: inline-block;
          width: 100%;
          min-width: 200px;
          flex: 1;
          height: 40px;
          border: 1px solid green;
          border-radius: 7px;
          padding: 0.6em 1em;
        }
      }
    }
  }
`;

export const UploadFilesComponent = ({ values, setFieldValue }) => {
  return (
    <UploadWrapper>
      <h2 className="title">Specify files to compare</h2>

      <div className="content">
        <div className="upload-box">
          <label htmlFor="file1">
            <span>select file 1</span>

            <div className="btn btn-outline-success" type="button">
              Browse
            </div>
          </label>
          <label htmlFor="file1">
            <span>select file 2</span>
            <div className="btn btn-outline-success" type="button">
              Browse
            </div>
          </label>

          <input
            type="file"
            className="visually-hidden"
            accept=".csv"
            id="file1"
            name="file1"
          />
          <input
            type="file"
            className="visually-hidden"
            accept=".csv"
            id="file2"
            name="file2"
          />
        </div>

        <button className="btn btn-primary mt-auto" type="submit">
          Compare
        </button>
      </div>
    </UploadWrapper>
  );
};
