import React, { useState } from "react";
import {
  Nav,
  NavItem,
  NavLink,
  Row,
  TabContent,
  Table,
  TabPane
} from "reactstrap";
import styled from "styled-components";

const PageWrapper = styled.div`
  padding: 0 2em;

  .content {
    width: 100%;
    padding: 1em 1.5em;

    .navitem {
      display: flex;
      overflow-x: hidden;
      align-items: center;
      justify-content: flex-start;
      border-bottom: 1px solid rgb(85, 216, 189);
      padding: 0;

      .navlink {
        flex: 1;
        border-radius: 0;
        padding: 1em;
        font-weight: 400;
        cursor: context-menu;
        border-bottom: 2px solid transparent;

        &:hover {
          font-weight: 500;
        }

        &.active {
          border-width: 2px;
          border-top-left-radius: 7px;
          border-top-right-radius: 7px;
          border-color: rgb(85, 216, 189);
          font-weight: 700;

          &::after {
            width: 100%;
          }
        }
      }
    }
  }
`;

export const ResultsMatchingComponent = () => {
  const [activeTab, setActiveTab] = useState(1);

  return (
    <PageWrapper>
      <h2 className="large bold">Unmatched Reports</h2>

      <div className="content">
        <Nav tabs className="navitem">
          <NavItem>
            <NavLink
              className={`navlink ${activeTab === 1 ? "active" : ""}`}
              onClick={() => setActiveTab(1)}
            >
              {" "}
              File 1
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={`navlink ${activeTab === 2 ? "active" : ""}`}
              onClick={() => setActiveTab(2)}
            >
              File 2
            </NavLink>
          </NavItem>
        </Nav>

        <TabContent activeTab={activeTab}>
          <TabPane tabId={1}>
            <Row>
              <Table responsive>
                <thead>
                  <tr>
                    <th>Transaction Date</th>
                    <th>Transaction Amount</th>
                    <th>Reference</th>
                    <th>Narrative</th>
                  </tr>
                </thead>
                <tbody>
                  {[].map((props) => (
                    <tr key={props.transactionId}>
                      <td></td>
                      <td></td>
                      <td></td>
                      <td></td>
                      <td></td>
                    </tr>
                  ))}
                </tbody>
              </Table>
            </Row>
          </TabPane>
          <TabPane tabId={2}>
            <Row>
              <Table responsive>
                <thead>
                  <tr>
                    <th>Transaction Date</th>
                    <th>Transaction Amount</th>
                    <th>Reference</th>
                    <th>Narrative</th>
                  </tr>
                </thead>
                <tbody>
                  {[].map((props) => (
                    <tr key={props.transactionId}>
                      <td></td>
                      <td></td>
                      <td></td>
                      <td></td>
                      <td></td>
                    </tr>
                  ))}
                </tbody>
              </Table>
            </Row>
          </TabPane>
        </TabContent>
      </div>
    </PageWrapper>
  );
};
